import React, { Component } from "react";
import { Jumbotron, Container } from "reactstrap";
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import ReactTypingEffect from "react-typing-effect";
import Carousel from "./Slider/Carousel";
import me3 from "../images/me3.png";
import AOS from "aos";
import "aos/dist/aos.css";
import Touch from "./Touch";
import { Link } from "react-router-dom";
import Skills from "./Skills";
import About from "./About";
class Home extends Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    window.scrollTo(0, 0);
    AOS.init({
      duration: 900,
      easing: "ease-in-out-back",
    });
  }
  render() {
    return (
      <div style={{ overflowX: "hidden" }}>
        <Jumbotron id="homeDestination">
          <Container>
            <div className="row">
              <div className="col-sm mt-5">
                <h1 id="hello">
                  <ReactTypingEffect
                    text={[
                      "Hello I'm Hassan",
                      "MERN Stack Developer",
                      "Graphic Designer",
                      "Glad to see you here",
                    ]}
                    speed="100"
                    eraseDelay="1000"
                    typingDelay="500"
                    style={{ color: "#f26d64" }}
                  />
                </h1>
              </div>
              <div className="col">
                <img src={me3} className="img-fluid" />
              </div>
            </div>
          </Container>
        </Jumbotron>
        <div className="row" align="center" id="touch" data-aos="fade-up">
          <div id="memeimage"></div>
          <div className="col-sm" id="education">
            <h1
              id="what"
              className="mt-3"
              data-aos="fade-up"
              data-aos-anchor-placement="bottom-bottom"
            >
              Me?
            </h1>
            <div className="row mt-4 mr-4 ml-4">
              <div
                className="col-md-8 offset-md-2"
                id="what2"
                data-aos="zoom-in-down"
                data-aos-anchor-placement="top-bottom"
              >
                <p id="me">
                  " I'm MERN-Stack Developer Always seeking for extending my
                  development knowledge, I write codes in React JS Javascript
                  based library, Supporting that by Node JS Back-End runtime,
                  Build REST API by Express JS Then Back it up by MongoDB "
                </p>
              </div>
            </div>
          </div>
        </div>

        <div data-aos="fade-up">
          <div className="text-center justify-content-center" id="backimage">
            <div id="frontcontent" className="">
              <div>
                <i
                  style={{ color: "#5491B7" }}
                  id="myi"
                  className="fas fa-server fa-5x"
                ></i>
              </div>
              <div>
                <span id="front">Back-End Developement</span>
              </div>
              <div className="row justify-content-center mt-3">
                <div id="KFS" className="col-sm-2">
                  <span className="fab fa-js-square"></span>
                  <span> JavaScript</span>
                </div>
                <div id="KFS" className="col-sm-2">
                  <span className="fab fa-node-js"></span>
                  <span> Node.Js</span>
                </div>
                <div id="KFS" className="col-sm-2">
                  <span>Express.JS</span>
                </div>
                <div id="KFS" className="col-sm-2">
                  <span>MongoDB</span>
                </div>
                <div id="KFS" className="col-sm-2">
                  <span
                    className="fab fa-git"
                    style={{ fontSize: "50px" }}
                  ></span>
                </div>
              </div>
            </div>
          </div>
          <div className="text-center justify-content-center" id="frontimage">
            <div id="a7a"></div>
            <div id="frontcontent">
              <div>
                <div>
                  <i
                    style={{ color: "#5491B7" }}
                    id="myi"
                    className="fas fa-file-code fa-5x"
                  ></i>
                </div>
                <span id="front">Front-End Developement</span>
              </div>

              <div className="row justify-content-center mt-3">
                <div id="KFS" className="col-sm-2">
                  <span className="fab fa-html5"></span>
                  <span> HTML</span>
                </div>
                <div id="KFS" className="col-sm-2">
                  <span className="fab fa-css3"></span>
                  <span> CSS</span>
                </div>
                <div id="KFS" className="col-sm-2">
                  <span className="fab fa-react"></span>
                  <span> REACT JS</span>
                </div>
                <div id="KFS" className="col-sm-2">
                  <span className="fab fa-bootstrap"></span>
                  <span> BOOSTRAP</span>
                </div>
                <div id="KFS" className="col-sm-2">
                  <span>jQUERY</span>
                </div>
                <div id="KFS" className="col-sm-2">
                  <span className="fab fa-js-square"></span>
                  <span> Javascript</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <About></About>
        <Container>
          <div className="row">
            <div className=" col-sm-3 ml-auto" data-aos="fade-left">
              <Touch></Touch>
            </div>
          </div>
        </Container>
      </div>
    );
  }
}

export default Home;
